import { useState, useRef, useEffect } from 'react'
import party from "party-js"
import seedrandom from 'seedrandom'

import './App.css'
import User from './data.json'
import Tree from './Tree'
import Ballon from './Ballon'
import Title from './title.png'
import { initsnow } from './snow'

seedrandom('2022-12-22', { global: true })

const shuffle = (array) => {
  let t, i
  let len = array.length
    while (len) {
        i = Math.floor(Math.random() * len --)
        t = array[len]
        array[len] = array[i]
        array[i] = t
    }
    return array
}

const getPresentNumber = (item, array) => {
  return array.findIndex(i => i === item) + 1
}

const SZers = shuffle(User.SZ.concat([]))
const SHers = shuffle(User.SH.concat([]))

function App() {
  const appRef = useRef(null)
  const btnRef = useRef(null)
  const [toggle, setToggle] = useState(false)
  const [email, setEmail] = useState('')
  const [error, setError] = useState(false)
  const [focused, setFocused] = useState(false)
  const [number, setNumber] = useState('--')

  useEffect(() => {
    initsnow(appRef.current)
  }, [])

  const handleInputChange = (ev) => {
    error && setError(false)
    setEmail((ev.target.value || '').toLowerCase())
  }
  const handleSubmit = () => {
    let number = null
    const fullEmail = `${email}@shopee.com`
    if (SZers.includes(fullEmail)) {
      number = getPresentNumber(fullEmail, SZers) + 30
    } else if (SHers.includes(fullEmail)) {
      number = getPresentNumber(fullEmail, SHers)
    } else {
      setError(true)
    }

    if (number) {
      party.confetti(btnRef.current, {
        count: party.variation.range(40, 60),
        size: party.variation.range(0.8, 1.2)
      })
      setNumber(number)
      setToggle(true)
    }
  }

  const handleClickResult= () => {
    toggle && setToggle(false)
  }

  return (
    <div ref={appRef} className="App">
      <Ballon />
      <Tree />
      <section className='main'>
        <div className='title'>
          <img className='img' alt='' src={Title} />
        </div>
        <div className={`card ${toggle ? 'toggled' : ''}`}>
          <div className='container'>
            <div className='form'>
              <div className='inputWrapper'>
                <div className={`input ${focused ? 'focused': ''}`}>
                  <input
                    value={email}
                    onFocus={() => setFocused(true)}
                    onBlur={() => setFocused(false)}
                    placeholder=''
                    autoComplete="off"
                    onChange={handleInputChange}
                  />
                  <div className='suffix'>@shopee.com</div>
                </div>
                {
                  error &&
                  <div className='error'>
                    {
                      !email ? '' : ''

                    }
                  </div>
                }
              </div>
              <div className='footer'>
                <div ref={btnRef} className='button' onClick={handleSubmit}></div>
              </div>
            </div>
            <div className='colorful' />
            <div className='result' onClick={handleClickResult}>
              <div className='content'>
                <div className='text'>
                  <div className='desc'></div>
                  <div className='number'>{number}</div>
                </div>
                <hr />
                <div className="subtext">* </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}

export default App;
